html,body {
  margin: 0;
  padding:0;
  overflow:hidden;
  max-width: 100vw;
  max-height:100vh;
  font-family: 'Helvetica Neue Cond';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

.App {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; 
}

.heading{
  margin-left: 50px;
  margin-bottom: 50px;;
}

h1, h2 {
  width: 100%;
  font-size: 76pt;
  line-height: 1;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  max-width: 735px;
}

.fontWeight-normal h1, .fontWeight-normal h2{
  font-weight: normal;
}

.logo{
  height: 90px;
  margin: 40px;
  align-self: flex-end;
}

.dataFutures-true .logo {
  align-self: flex-start;
}

.data-futures{
  display: none;
  position: absolute;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 50%;
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.dataFutures-true .data-futures,
.dataFutures-true .team-name{
  display: block;
}

.dataFutures-true h1,
.dataFutures-true h2{
  width: 540px;
}

.team-name{
  position: absolute;
  display: none;
  right: 265px;
  top: 40px;
  width: 100px;
  height: 100px;
  font-size: 22px;
  text-transform: uppercase;
}

.team-name::first-line {
  color: #006bab;
}

.App:not(.logo-default):not(.logo-60-year-lockup){
  justify-content: center;
}

.App:not(.logo-default):not(.logo-60-year-lockup) .heading{
  margin-bottom: 0;
}

.type-footer .logo,
.type-footer .heading,
.type-footer .data-futures,
.type-footer .team-name{
  display: none;
}

.mal{
  display: none;
  height: 65%;
  margin-left: 25px;
}

.type-footer .mal{
  display: block;
}
.shape-container {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
}

.svg-shapes {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100%;
  margin: 0;
}

.svg-shapes circle {
  fill: transparent;
  stroke: url(#bggrad);
  stroke-linecap: round;
  -webkit-animation: bgmove 200s linear infinite;
          animation: bgmove 200s linear infinite;
}

@-webkit-keyframes bgmove {
  0% {
    stroke-dashoffset: 1000;
  }

  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes bgmove {
  0% {
    stroke-dashoffset: 1000;
  }

  100% {
    stroke-dashoffset: 0;
  }
}

.logos {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: 1fr 0.25fr 1fr;
  place-items: center;
  background-color: #1f2a44;
  padding: clamp( 0.5rem, 5vw, 2rem);
  border-radius: 3rem;
  z-index: 2;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
}

.logos svg {
  display: block;
  width: clamp( 1rem, 5vw, 3rem);
  height: clamp( 1rem, 5vw, 3rem);
}

.seperator {
  place-self: flex-end;
  border-left: 2px solid white;
  height: 100%;
}

